.post {
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  padding: 10px;
  background: #9370db;
  margin: 0 auto 30px;
  width: 60%;
  position: relative;
}

.delete {
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

.post:hover .delete-post,
dt:hover .delete-comment {
  opacity: 1;
}

h1 {
  text-align: center;
}

h2 {
  max-width: max-content;
  color: white;
}

.author {
  color: #4b0082;
  font-size: 12px;
  font-style: italic;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

dl {
  margin: 0;
}

dt {
  color: #37075a;
  position: relative;
}

dd {
  box-sizing: border-box;
  padding: 5px;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 10px;
  border: 1px solid purple;
}

.shadow {
  box-shadow: 10px 5px 5px grey;
}

button {
  display: block;
  margin: auto;
}

h3 {
  max-width: max-content;
  margin: auto;
}

.filter {
  display: block;
  margin: 10px auto;
  border: 1px solid grey;
  height: 30px;
  border-radius: 15px;
  padding: 5px;
  outline: none;
  font-size: 20px;
}
